*{
  box-sizing: border-box;
  margin:0;
  padding: 0;
}
body{
  font-family: 'Source sans pro', sans-serif;
}

.main{
  width:100%;
  display: flex;
  padding: 7% 12%;
  align-items: center;
  justify-content: center;
}
.previewProduct{
  width:50%;
}
.descriptionSection{
  width:50%;
}
@media(max-width:600px) {
  .main{
    flex-direction: column;
    padding: 15% 10%;
  }
  .previewProduct{
    width:100%;
  }
  .descriptionSection{
    width:100%;
  }
}