.descriptionSection{
    width:100%;
}
.productName{
    font-size: 48px;
    font-weight: 600;
    margin-top:0;
    margin-bottom: 16px;
}
.productDescription{
    font-size: 20px;
    color:rgba(0,0,0,0.8);
    margin-top:0;
    margin-bottom: 16px;
}
.colourOptions{
    font-size: 28px;
    margin-bottom: 12px;
}
.watchImage{
    width:100%;
    max-width: 100px;
    border: 2px solid transparent;
    border-radius: 8px;
    margin-right: 12px;
    cursor: pointer;
} 
.watchImageSelected{
    border:2px solid teal;
}
.featureItem{
    min-width: 80px;
    background-color: gainsboro;
    border-radius: 8px;
    border:0;
    padding: 12px 24px;
    margin-right:12px;
    text-align: center;
    cursor:pointer;
    font-weight: 600;
    box-sizing: content-box;
}
.featureItemSelected{
    color:#fff;
    background-color: gray;
}

.primaryButton{
    cursor: pointer;
    color:white;
    background-color: teal;
    border:0;
    font-size: 20px;
    border-radius:8px;
    padding: 12px 24px;
    margin-top: 36px;
}
@media(max-width:600px){
    .productName{
        font-size: 38px;
    }
    .productDescription{
        font-size: 18px;
    }
    .colourOptions{
        font-size: 28px;
    }
}