.previewProduct{
    width:100%;
    position: relative;
}
.watchDisplay{
  position: absolute;
  left:50%;
  top:50%;
  color:#fff;
  font-size: 48px;
  font-weight: 600;
  transform: translate(-50%,-50%);
}
.hbDisplay{
  position: absolute;
  left:50%;
  top:50%;
  color:#fff;
  font-size: 48px;
  font-weight: 600;
  transform: translate(-50%,-50%);
}
.hbDisplay > p{
  margin-top: 10px;
}
.hbDisplay > i{
  color:red;
  animation: beat 1s infinite;
}
@keyframes beat{
  0%{
    transform: scale(1,1);
  }
  50%{
    transform: scale(1.2, 1.2);
  }
  100%{
    transform: scale(1, 1);
  }
}
.previewProduct > img{
  width:100%;
}
  
@media(max-width:600px){
  .watchDisplay, .hbDispla{
    font-size: 38px;
  }
}